import amap from '../../components/ms-openMap/openMap.js'
export default {
	data() {
		return {
			amapPlugin: null,
			key: 'a930035635c1ea505392af9f41ee25a2'
		}
},
onLoad() {
this.amapPlugin = new amap.AMapWX({
key: this.key
});
},
methods: {
	getRegeo() {
	uni.showLoading({
		title:'‘获取信息中’'
	});
	this.amapPlugin.getRegeo({
			success: (data) => {
				console.log(data)
				this.addressName = data[0].name;
				uni.hideLoading();
				}
			});
		}
	}
}
